html,
body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

.page {
  font-family: Cinzel, serif;
  color: $text-main-color;
  scroll-behavior: smooth;

  &__body {
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translate(0);
    }
  }
}

p {
  padding: 0;
}

a {
  cursor: pointer;
}
